import { Box, Container, Flex, HStack, position, Spacer, Text, VStack } from "@chakra-ui/react";

export default function Navbar(){
    return(
       <Box w="100%" color="whiteAlpha.800" fontSize={20} zIndex={10} position="fixed"  backgroundColor="#1a232e" >
        <Flex padding={5} w="90%" margin="auto" gap={5}>
            <VStack>
               <Text>Home</Text>
            </VStack>
            <Spacer/>
         <VStack >
           <Flex gap={10}>
           <Text>About</Text>
            <Text>Skills</Text>
            <Text>Projects</Text>
            <Text>Contact</Text>
            <Text>Resume</Text>
           </Flex>
           </VStack>
      <Spacer/>
           <Flex gap={5}>
            <Text>Github</Text>
            <Text>Linkedin</Text>
           </Flex>
            </Flex>
       </Box>
    )
}