import { Box, Container, Text, VStack } from "@chakra-ui/react";
const breakpoints = {
    base:"center",
    sm: 'center',
    md: 'center',
    lg: 'start',
    xl: 'start',
    '2xl': 'start',
    
  }
  const breakpoints1 = {
    base:"18px",
    sm: '30px',
    md: '50px',
    lg: '50px',
    xl: '50px',
    '2xl': '50px',
  }
export default function Info(){
return(
    <Box width="100%">
       <VStack align={breakpoints}> <Text fontSize={breakpoints1} fontWeight="semibold" letterSpacing="1px"  color="whiteAlpha.800">Hi There !</Text>
       <Text pt={0} fontSize={breakpoints1} fontWeight="semibold" letterSpacing="1px"  color="whiteAlpha.800">I am Avadhut Shinde</Text></VStack>
    </Box>
)
}